"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import toast from "react-hot-toast";

import { apiFetch } from "@/lib/api-client";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";

type Enrollment = {
  id: number;
  status: "pending" | "approved" | "rejected" | "dropped";
  remarks: string | null;
  requested_at: string | null;
  student_id: number;
  student_name: string;
  student_email: string;
  course_id: number;
  course_code: string;
  course_title: string;
};

export default function AdminEnrollmentsPage() {
  const [rows, setRows] = useState<Enrollment[]>([]);
  const [loading, setLoading] = useState(true);
  const [remarks, setRemarks] = useState<Record<number, string>>({});

  const loadData = async () => {
    try {
      setLoading(true);
      const res = await apiFetch("/admin/enrollments");
      setRows((res as { enrollments: Enrollment[] }).enrollments ?? []);
    } catch (error) {
      toast.error(error instanceof Error ? error.message : "Failed to load enrollment requests.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadData();
  }, []);

  const decide = async (id: number, status: "approved" | "rejected") => {
    try {
      await apiFetch(`/admin/enrollments/${id}`, {
        method: "PATCH",
        body: JSON.stringify({
          status,
          remarks: remarks[id] || null,
        }),
      });
      toast.success(`Enrollment ${status}.`);
      loadData();
    } catch (error) {
      toast.error(error instanceof Error ? error.message : "Failed to update enrollment.");
    }
  };

  return (
    <main className="min-h-screen bg-slate-50 p-6">
      <div className="max-w-6xl mx-auto space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-slate-900">Enrollment Management</h1>
            <p className="text-slate-600">Review and approve/reject student enrollment requests.</p>
          </div>
          <Link href="/admin">
            <Button variant="outline">Back to Admin</Button>
          </Link>
        </div>

        <Card>
          <CardHeader>
            <CardTitle>Requests</CardTitle>
            <CardDescription>Pending and historical enrollment requests.</CardDescription>
          </CardHeader>
          <CardContent>
            {loading ? (
              <p className="text-slate-500">Loading requests...</p>
            ) : rows.length === 0 ? (
              <p className="text-slate-500">No enrollment requests found.</p>
            ) : (
              <div className="space-y-4">
                {rows.map((row) => (
                  <div key={row.id} className="border rounded-lg p-4 space-y-3">
                    <div className="flex items-start justify-between gap-3">
                      <div>
                        <p className="font-semibold text-slate-900">{row.course_code} - {row.course_title}</p>
                        <p className="text-sm text-slate-700">{row.student_name} ({row.student_email})</p>
                        <p className="text-xs text-slate-500">Requested: {row.requested_at ? new Date(row.requested_at).toLocaleString() : "-"}</p>
                        {row.remarks && <p className="text-xs text-amber-700">Remarks: {row.remarks}</p>}
                      </div>
                      <Badge>{row.status}</Badge>
                    </div>

                    {row.status === "pending" && (
                      <div className="flex items-center gap-2">
                        <Input
                          placeholder="Optional remarks"
                          value={remarks[row.id] ?? ""}
                          onChange={(e) => setRemarks((prev) => ({ ...prev, [row.id]: e.target.value }))}
                        />
                        <Button onClick={() => decide(row.id, "approved")}>Approve</Button>
                        <Button variant="destructive" onClick={() => decide(row.id, "rejected")}>Reject</Button>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </main>
  );
}
